import React from 'react';
import {Breadcrumb, Col, Row, Table, Card, Affix, Menu, Dropdown, Button, Icon, ConfigProvider} from 'antd';
import ReactEcharts from 'echarts-for-react';
import {Scrollbars} from 'react-custom-scrollbars';
import url from 'url';
import moment from 'moment';
import {inject} from "mobx-react";

const customizeRenderEmpty = () => (
	<div style={{ textAlign: 'center',padding: '50px 0'}}>
		<img
			width='150px'
			height="100px"
			src='./images/not_found.png' />
		<p style={{
			fontSize:'14px',
			fontFamily:'PingFangSC-Regular,PingFang S',
			fontWeight: '400',
			color: 'rgba(75,80,81,1)'
		}}>暂无数据</p>
	</div>
);

@inject('kanbanStore')
export default class Xmqk extends React.Component {
	constructor(props) {
		super(props);
		this.state = {
			yearData: [{
				id: '',
				name: '2019'
			}],  // 年份
			selData: [
				{
					id: '01',
					name: '1月'
				}, {
					id: '02',
					name: '2月'
				}, {
					id: '03',
					name: '3月'
				}, {
					id: '04',
					name: '4月'
				}, {
					id: '05',
					name: '5月'
				}, {
					id: '06',
					name: '6月'
				}, {
					id: '07',
					name: '7月'
				}, {
					id: '08',
					name: '8月'
				}, {
					id: '09',
					name: '9月'
				}, {
					id: '10',
					name: '10月'
				}, {
					id: '11',
					name: '11月'
				}, {
					id: '12',
					name: '12月'
				},
			],
			value: '',
			value1: '2019',
			id: '', // 合同表id
			key: '3',
			month: '', //月份
			time: null, // 最后更新时间
			capitalData: [], // 数据
			tableData: [],  // 多个图表数据
			columns: [],
			columns0: [      // 已交付
				{
					title: '区域',
					dataIndex: 'region',
					align: 'center',
					className: 'vt',
					render: (text, row, index) => {
						if (text.rows || text.rows == 0) {
							return {
								children: <span>{text.label}</span>,
								props: {
									rowSpan: text.rows,
									className: 'th_bg vt'
								}
							}
						}
						if (text.cols) {
							return {
								children: <span>{text.label}</span>,
								props: {
									colSpan: text.cols,
									className: 'th_bg vt'
								}
							}
						}
					},
				},
				{
					title: '产品',
					dataIndex: 'product',
					align: 'center',
					className: 'vt',
					render: (text, row, index) => {
						const obj = {
							children: text,
							props: {
								className: 'th_bg'
							},
						};
						if (row.region.cols) {
							obj.props.colSpan = 0;
						}
						return obj;
					}
				},
				/*{
					title: '合计',
					dataIndex: 'total',
					align: 'center',
					className: 'vt',
				},*/
				{
					title: '已交付',
					dataIndex: '',
					children: [
						{
							title: '进度',
							dataIndex: '',
							align: 'center',
							children: [
								{
									title: '提前',
									dataIndex: 'deliveredProgressEarly',
									align: 'center',
									className: 'vt th_bg1',
								},
								{
									title: '正常',
									dataIndex: 'deliveredProgressNormal',
									align: 'center',
									className: 'vt th_bg1',
								},
								{
									title: '延迟',
									dataIndex: 'deliveredProgressDelay',
									align: 'center',
									className: 'vt th_bg1',
								},
							]
						},
						{
							title: '成本',
							dataIndex: '',
							align: 'center',
							children: [
								{
									title: '低于预算',
									dataIndex: 'deliveredCostLower',
									align: 'center',
									className: 'vt th_bg1',
								},
								{
									title: '正常',
									dataIndex: 'deliveredCostNormal',
									align: 'center',
									className: 'vt th_bg1',
								},
								{
									title: '高于预算',
									dataIndex: 'deliveredCostHigher',
									align: 'center',
									className: 'vt th_bg1',
								},
							]
						},
					],
				},
			],
		columns1: [   // 待交付
			{
				title: '区域',
				dataIndex: 'region',
				align: 'center',
				className: 'vt',
				render: (text, row, index) => {
					if (text.rows || text.rows == 0) {
						return {
							children: <span>{text.label}</span>,
							props: {
								rowSpan: text.rows,
								className: 'th_bg vt'
							}
						}
					}
					if (text.cols) {
						return {
							children: <span>{text.label}</span>,
							props: {
								colSpan: text.cols,
								className: 'th_bg vt'
							}
						}
					}
				},
			},
			{
				title: '产品',
				dataIndex: 'product',
				align: 'center',
				className: 'vt',
				render: (text, row, index) => {
					const obj = {
						children: text,
						props: {
							className: 'th_bg'
						},
					};
					if (row.region.cols) {
						obj.props.colSpan = 0;
					}
					return obj;
				}
			},
			/*{
				title: '合计',
				dataIndex: 'total',
				align: 'center',
				className: 'vt',
			},*/
			{
				title: '待交付',
				dataIndex: '',
				children: [
					{
						title: '进度',
						dataIndex: '',
						align: 'center',
						children: [
							{
								title: '提前',
								dataIndex: 'constructionProgressEarly',
								align: 'center',
								className: 'vt th_bg1',
							},
							{
								title: '正常',
								dataIndex: 'constructionProgressNormal',
								align: 'center',
								className: 'vt th_bg1',
							},
							{
								title: '延迟',
								dataIndex: 'constructionProgressDelay',
								align: 'center',
								className: 'vt th_bg1',
							},
						]
					},
					{
						title: '成本',
						dataIndex: '',
						align: 'center',
						children: [
							{
								title: '低于预算',
								dataIndex: 'constructionCostLower',
								align: 'center',
								className: 'vt th_bg1',
							},
							{
								title: '正常',
								dataIndex: 'constructionCostNormal',
								align: 'center',
								className: 'vt th_bg1',
							},
							{
								title: '高于预算',
								dataIndex: 'constructionCostHigher',
								align: 'center',
								className: 'vt th_bg1',
							},
						]
					},
				],
			},
		]
		};
	}

	componentDidMount() {
		// 获取当前年份数据
		const currentYear = moment().year();
		/*
		* 年份数据来源：从2019年开始， 截至到当前的年份
		* */
		if (currentYear<=2019) {
			this.setState({
				yearData: [{
					name: '2019'
				}]
			});
		} else {
			const num = Number(currentYear);
			let data = [];
			for (let i=2019;i<=num;i++) {
				data.push({name:String(i)})
			}
			this.setState({
				yearData: Object.assign([], data.reverse())
			});
		}
		const data = {
			'01': '1月',
			'02': '2月',
			'03': '3月',
			'04': '4月',
			'05': '5月',
			'06': '6月',
			'07': '7月',
			'08': '8月',
			'09': '9月',
			'10': '10月',
			'11': '11月',
			'12': '12月'
		}
		let search = this.props.location.search;
		const id = url.parse(search, true).query.id;
		const time = url.parse(search, true).query.time;
		const key = url.parse(search, true).query.key;
		const mont = moment().month() + 1;
		const mon = url.parse(search, true).query.month;
		let month = mon == undefined ? mont : mon;
		let lastTime = null; // 最后更新时间

		// 没有时间
		if(time==undefined) {
			// 根据id查询时间
			this.getLastUpdatedTime().then((res) => {
				if (res) {
					let capitalInfo = res.filter(item => {
						return item.code === "xmqk"
					});
					if (capitalInfo.length > 0) {
						lastTime = capitalInfo[0]['lastUpdateTime'];
						this.setState({
							time: Number(lastTime)
						})
					}
				}
			});
		} else {
			this.setState({
				time: Number(time)
			})
		}

		this.setState({
			id,
			key,
            value: data[month],
			month: month.toString(),
			value1: currentYear.toString()
		}, () => {
			// 获取初始数据
			this.getReportByTime();
		});
	}

	// 获取最后时间
	getLastUpdatedTime = () => {
		const params = {
			"level": 2,
			"parentId": ""
		}
		return this.props.kanbanStore.getLastTime(params);
	}

	getReportByTime = () => {
		const {id, month, value1} = this.state;
		let params = {"classifyId": id, "year": value1, "beginMonth": month, "endMonth": month};
		if (month === "season1") {
			params = {"classifyId": id, "year": value1, "beginMonth": "01", "endMonth": "03"};
		} else if (month === "season2") {
			params = {"classifyId": id, "year": value1, "beginMonth": "04", "endMonth": "06"};
		} else if (month === "season3") {
			params = {"classifyId": id, "year": value1, "beginMonth": "07", "endMonth": "09"};
		} else if (month === "season4") {
			params = {"classifyId": id, "year": value1, "beginMonth": "10", "endMonth": "12"};
		} else if (month === "fullyear") {
			params = {"classifyId": id, "year": value1, "beginMonth": "01", "endMonth": "12"};
		}

		this.props.kanbanStore.getReportByClassifyId(params).then((res) => {
			if (res) {
				this.setState({
					columns: res.filter((item) => {
						return item.name === "项目情况汇总表";
					})[0].schemas.map((item)=> {
						return{
							title: item.title,
							dataIndex: item.name,
							align: 'center',
							className: 'vt',
							children: item.children.map((v)=>{
								return {
									title: v.title,
									dataIndex: v.name,
									align: 'center',
									className: 'vt th_bg1',
								}
							})
						}
					}),
					capitalData: Object.assign([], res.filter((item) => {
						return item.name === "项目情况汇总表";
					})[0]['datas']),
					tableData:  res.filter((item) => {
						return item.name !== "项目情况汇总表";
					})
				})
			}
		});
	}

	handleMenuClick = (e) => {
		let value = e.key;
		this.setState({
			value: e.item.props.children,
			month: value
		}, ()=>{
			this.getReportByTime();
		});
	}

	// 切换年份
	handleYearClick = (e) => {
		this.setState({
			value1: e.item.props.children
		},()=>{
			this.getReportByTime();
		})
	}

	// 动态图表
	changeOption = (params)  => {
		return {
			title: {
				text: params.chartName+"图",
				left:'center',
				textStyle:{
					color: 'rgb(61, 77, 102)',
					fontWeight:'bold',
					fontStyle:'normal',
					fontFamily: 'Microsoft YaHei UI',
					fontSize:'13'
				}
			},
			color: ['#47BAC1','#7FC8EA','#B5E3E5','#6DD4B3','#A6E59F', '#59BFCB',],
			tooltip : {
				trigger: 'axis',
				axisPointer : {
					type : 'shadow'
				}
			},
			legend: {
				data: params.series.length?params.series.map((item) => {return item.name}):[],
				y: 'bottom',
			},
			grid: {
				left: '3%',
				right: '3%',
				containLabel: true
			},
			xAxis : [
				{
					type : 'category',
					data : params.category
				}
			],
			yAxis : [
				{
					type : 'value',
					scale : true,
					axisLine:{
						show:false
					},
					axisTick:{
						show:false
					},
				}
			],
			series : params.series.length?params.series.map((item) => {
				return {
					name: item.name,
					type: 'bar',
					stack: item.stack,
					barWidth: '45%',
					data: item.data,
					itemStyle: {
						emphasis: {
							barBorderRadius: 1.5
						},
						normal: {
							barBorderRadius: 1.5
						}
					},
				}
			}):[]
		}
	}


	render() {
		const {yearData, selData, value, value1, time, capitalData, columns, tableData,key} = this.state;
		const menu = (
			<Scrollbars style={{height: 200}}>
				<Menu className="con_menu" onClick={this.handleMenuClick}>
					{selData.map((item, index) => {
						return (
							<Menu.Item key={item.id}>
								{item.name}
							</Menu.Item>
						)
					})}
				</Menu>
			</Scrollbars>
		);
		const menu1 = (
			<Scrollbars style={{height: 200}}>
				<Menu className="con_menu" onClick={this.handleYearClick}>
					{yearData.map((item, index) => {
						return (
							<Menu.Item key={'year' + index}>
								{item.name}
							</Menu.Item>
						)
					})}
				</Menu>
			</Scrollbars>
		);

		const {defaultKey} = this.props.kanbanStore;

		return (
			<div>
				<ConfigProvider renderEmpty={customizeRenderEmpty}>
				<div className="con_bottom">
					<Breadcrumb separator=">" style={{margin: '0'}}>
						<Breadcrumb.Item href={"#/home?key=" + defaultKey}>经营看板</Breadcrumb.Item>
						<Breadcrumb.Item href={"#/home?key=" + key}>项目</Breadcrumb.Item>
						<Breadcrumb.Item>项目情况表</Breadcrumb.Item>
					</Breadcrumb>
				</div>
				<Row>
					<Col span={24}>
						<div className="mt20 mb20">
							<span className="con_title">项目情况表 </span>
							<span className="con_time ml35">最近更新时间:</span><span
							className="con_time_content ml15">{moment(time).format("YYYY-MM-DD HH:mm:ss")}</span>
						</div>
					</Col>
					<Col span={24}>
						<Dropdown overlay={menu1}>
							<Button style={{width: 82}}>
								{value1} <Icon type="down"/>
							</Button>
						</Dropdown>
						<Dropdown overlay={menu}>
							<Button className="ml15" style={{width: 82}}>
								{value} <Icon type="down"/>
							</Button>
						</Dropdown>
					</Col>
				</Row>
				<Card bordered className="mt20" style={{width: '100%'}}>
					<h3 className="kanban_title mb20">项目情况汇总表（单位：家）</h3>
					<Table columns={columns}
					       dataSource={capitalData}
					       size={"small"}
					       pagination={false}
					       className="ci_table2"
					       title={() => '项目情况汇总表'}
					       rowClassName={(record, index) => {
						       let className = index % 2 ? 'deep_gray' : 'shallow_gray';
						       return className
					       }}
					       bordered/>
				</Card>
				{tableData&&tableData.map((item, index)=>{
					return (<Card key={'card_'+index} bordered className="mt20" style={{width: '100%'}}>
							<h3 className="kanban_title mb20">{item.name}（单位：家）</h3>
							<Row>
								<Col span={10}>
									<ReactEcharts
										notMerge={true}
										lazyUpdate={true}
										option={this.changeOption(item.charts)}/>
								</Col>
								<Col span={14}>
									<Table columns={this.state['columns'+index]}
									       dataSource={item.datas}
									       size={"small"}
									       className="ci_table2 ci_table4"
									       pagination={false}
									       title={() => value + item.name+"明细"}
									       rowClassName={(record, index) => {
										       let className = index % 2 ? 'shallow_gray' : 'shallow_blue';
										       return className
									       }}
									       bordered/>
								</Col>
							</Row>
						</Card>
					)
				})}
				</ConfigProvider>
			</div>
		);
	}
}






